}

.footer {
  padding-top: 30px;
  padding-bottom: 40px;
  background-color: #555555;
  color: #fff;
}
@media only screen and (min-width: 768px) {
  .footer {
    padding-top: 40px;
    padding-bottom: 60px;
  }
}
.footer__logo {
  font-family: "M PLUS Rounded 1c", "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  font-size: 32px;
  font-weight: 900;
  letter-spacing: 1.28px;
  opacity: 1;
}

.footer-info {
  margin-top: 24px;
  display: flex;
  flex-direction: column;
  font-size: 14px;
  color: #ccc;
}
@media only screen and (min-width: 768px) {
  .footer-info {
    flex-direction: row;
    justify-content: space-between;
  }
}

.footer-link {
  margin-bottom: 8px;
}
.footer-link__child:not(:last-of-type) {
  margin-right: 8px;
}
@media only screen and (min-width: 768px) {
  .footer-link {
    margin-bottom: 0px;
  }
}

